import React from "react";
import ArrowScroll from "./ArrowScroll.jsx"

const AboutMe = (props) => {
    return <div className="aboutMe section" ref={props.aRef} style={props.mSDisplay}>
        <p className="sectionHeader">About Me</p>
        <div className="aboutMe__logo"></div>
        <p className="aboutMe__text">Siemanko. Jestem zwykłym człowiekiem, który od zawsze posiadał w sobie ogromne zamiłowanie do muzyki. Już jako 6 letni małolat nagrywałem z kumplem i jego siostrą tzw. "kocią muzykę" - każde napieprzało bez składu i ładu - czy to w cymbałki, czy kijkiem o krzesło, czy też pluło z zapałem we flet. To wszystko było nagrywane na klasycznym magnetofonie marki Hitachi sprowadzonym prosto z Singapuru. Zamiłowanie do przygrywek pozostało ze mną. Uczęszczałem do osiedlowego klubiku żeby rozwijać umiejętności gry na keyboardzie, przez co matka zapisała mnie później do szkoły muzycznej. Po jakimś czasie odkryłem Fruity Loops`a... ;)</p>
        <ArrowScroll handleScroll={props.handleScroll} part="cRef" text="Dalej"/>
    </div>
}

export default AboutMe;