import React from "react";
import Arrow from './arrowDownWhite.png';

const ArrowScroll = (props) => {
    return <div className="scrollText" onClick={props.handleScroll} part={props.part} menu="no-menu">
        <p part={props.part} menu="no-menu">{props.text}</p>
        <img src={Arrow} alt="scroll down arrow" part={props.part} menu="no-menu"></img>
    </div>
}

export default ArrowScroll;