import React from "react";
import mail from './email.png';
import ArrowScroll from "./ArrowScroll.jsx"

const Contact = (props) => {
    return <div id="contact" className="contact section" ref={props.cRef} style={props.mSDisplay}>
        <p className="sectionHeader">Contact</p>
        <div className="contact__container">
        <p className="contact__text">Jeżeli jesteś zainteresowany współpracą lub też chcesz wyrazić swoją opinię na temat mojej muzyki:</p>
            {/* <div className="contact__mediaContainer">
                <img src={phone} className="contact__phoneIco"></img><span>500-697-243</span>
            </div> */}
            <div className="contact__mediaContainer">
                <img src={mail} className="contact__mailIco" alt="mail icon"></img><span>szamon@o2.pl</span>
            </div>
        </div>
        <ArrowScroll handleScroll={props.handleScroll} part="tRef" text="Do początku"/>
    </div>
}

export default Contact;